<template>
  <div style="padding-top: 100px;">
    <v-card class="mx-auto" max-width="450" height="350">
      <v-card-actions>
        <v-file-input style="display: none"
          ref="input"
          type="file"     
          accept="image/*" 
          @change="changeAvatar2()"
        /> 
        <v-avatar size="88" class="mx-auto mt-2">
          <v-img
            :src="userAvatar"
            @click="changeAvatar()"
          />
        </v-avatar>
      </v-card-actions>
      <v-container>
        <v-form>
          <v-row
            v-for="item in myInfo"
            :key="item.name"
            align="center"
            justify="center"
            no-gutters
          >
            <v-col cols="2" style="text-align: end;">
              <p>{{ item.name }}</p>
            </v-col>
            <v-col cols="6">
              <v-text-field
                v-model="item.model"
                :disabled="item.disabled"
                dense
              />
            </v-col>
          </v-row>
          <v-row
            align="center"
            justify="center"
            no-gutters
          >
            <v-col style="text-align: center;">
              <v-btn
                depressed
                dark
                color="primary"
                width="100"
                :loading="setUserNickNameLoading"
                @click="setNickname"
              ></v-btn>
            </v-col>
          </v-row>
        </v-form>
      </v-container>
    </v-card>
    <v-dialog v-model="cropperDialog" width="510">
    <v-card>
      <div style="height: 400px;">
        <vueCropper
          ref="cropper"
          :fixed="option.fixed"
          :centerBox="option.centerBox"
          :img="option.img"
          :outputSize="option.size"
          :outputType="option.outputType"
          :maxImgSize="option.maxImgSize"
          :autoCrop="option.autoCrop"
          :canMove="option.canMove"
        ></vueCropper>
      </div>
      <v-card-actions>
        <v-spacer/> 
        <v-btn
          depressed
          dark
          color="primary"
          :loading="setAvatarLoading"
          @click="finish()"
        ></v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
  </div>
</template>

<script>
import { VueCropper }  from 'vue-cropper'
import{ mapGetters } from 'vuex'
export default {
  components: {
      VueCropper,
    },
  data: () => ({
    setUserNickNameLoading: false,
    setAvatarLoading: false,
    cropperDialog: false,
    modelAvatar: '',
    setUserNicknameParam: {
      nickname: '',
    },
    option: {
      img: '', //
      size: 0.5, //
      fixed: true,
      centerBox: true,
      autoCrop: true,
      outputType: 'jpeg', //
      maxImgSize: 500, //
      canMove: false //
    }
  }),

  computed: {
    ...mapGetters(['username', 'nickname', 'email', 'userAvatar']),
    myInfo() {
      return [
        { name: '',  disabled: false, model: this.nickname },
        { name: '', disabled: true, model: this.username },
        { name: '',  disabled: true, model: this.email }
      ]
    }
  },

  created() {
    // this.$store.dispatch('getUserAvatar')
  },

  methods: {
    setNickname() {
      this.setUserNickNameLoading = true
      this.setUserNicknameParam.nickname = this.myInfo[0].model
      this.$store.dispatch('setUserComInfo', this.setUserNicknameParam).then(() => {
        this.setUserNickNameLoading = false
      }).catch(() => {
        this.setUserNickNameLoading = false
      })
    },
    changeAvatar(){
      this.$refs.input.$refs.input.click()
    },
    changeAvatar2() {
      this.cropperDialog = true
      // console.log(e)
      // console.log(event)
      // console.log(event.target.value)
      this.option.img = ''
      var file = event.target.files[0]
      if (!/\.(gif|jpg|jpeg|png|bmp|GIF|JPG|PNG)$/.test(event.target.value)) {
        alert('.gif,jpeg,jpg,png,bmp')
        return false
      }
      var reader = new FileReader()
      reader.onload = (e) => {
        let data
        // console.log(e.target.result)
        data = e.target.result
        // console.log(e.target.result)
        if (typeof e.target.result === 'object') {
          // Array Bufferblob base64
          data = window.URL.createObjectURL(new Blob([e.target.result]))
        } else {
          data = e.target.result
        }
        this.option.img = data
      }
      reader.readAsArrayBuffer(file)
    },
    finish() {
      this.$refs.cropper.getCropData((data) => {
        const file = new window.File([data], 'avatar', { type: 'image/jpeg' })
        const formData = new FormData();
        formData.append('file', file)
        console.log(formData.get("file"))
        let object = {};
        object = {
          formData: formData,
          data: data,
        }
        this.setAvatarLoading = true
        this.$store.dispatch('setUserAvatar', object).then(() => {
          this.cropperDialog = false
          this.setAvatarLoading = false
        })
      })
    },
  }
}
</script>